import { intersects } from "radash"

function recurse(node: any, roles: any) {
	if (!node.children || node.children.length < 1) {
		if (Array.isArray(node.permissions) && node.permissions.length < 1) {
			return true
		}
		const doesIntersects = intersects(roles, node.permissions)
		return doesIntersects
	} else if (
		intersects(roles, node.permissions) ||
		(Array.isArray(node.permissions) && node.permissions.length < 1)
	) {
		node.children = node.children.filter((ele: any) => {
			return recurse(ele, roles)
		})
		return true
	} else {
		return false
	}
}
export { recurse }
