import { Alert, AlertTitle, Snackbar, Typography } from "@mui/material"
import {
	Navigate,
	Route,
	RouteObject,
	RouteProps,
	Routes,
	useRoutes
} from "react-router-dom"
import AppLayout from "./layout/AppLayout"
import AccountAccess from "./pages/AccountAccess"
import AddUser from "./pages/AddUser"
import AuditTrail from "./pages/AuditTrail"
import ForgotPassword from "./pages/ForgotPassword"
import { Home } from "./pages/Home"
import Login from "./pages/Login"
import ResetPassword from "./pages/ResetPassword"
import { UserManagement } from "./pages/UserManagement"
import Protected from "./components/Protected"
import TestRoute from "./pages/TestRoute"
import Profile from "./pages/Profile"
import CreateInternalUser from "./pages/CreateInternalUser"
import { UserProvider } from "./context/UserContext/UserContext"
import { useAppContext } from "./context/AppContext/AppContext"
import { SContainer } from "./components/AuthContainer/index.styles"
import { useMemo, useState } from "react"
import { recurse } from "./utils/getAuthRoles"
import produce from "immer"
import { TRoles } from "./context/AuthContext/AuthContext.types"
import { useAuthContext } from "./context/AuthContext/AuthContext"

interface SingleRouteObject extends RouteObject {
	children?: RoutesType[]
}
interface RoutesType extends SingleRouteObject {
	permissions: TRoles[]
}

function App() {
	const appState = useAppContext()
	const authState = useAuthContext()

	// const recurseRoutes = (originalRoutes: RoutesType[]) => {
	// 	const { routes } = produce({ routes: originalRoutes }, draft => {
	// 		draft.routes = draft.routes.filter((root: any) => {
	// 			return recurse(root, authState?.rolesInfo.roles || [])
	// 		})
	// 	})
	// 	return routes
	// }

	const recurseRoutes = (originalRoutes: RoutesType[]) => {
		return originalRoutes.filter((root: any) => {
			return recurse(root, authState?.rolesInfo.roles || [])
		})
	}

	const resolvedRoutes: RoutesType[] = useMemo(() => {
		return recurseRoutes([
			{
				path: "sign-in",
				element: <Login />,
				permissions: []
			},
			{
				path: "forgot-password",
				element: <ForgotPassword />,
				permissions: []
			},
			{
				path: "reset-password",
				element: <ResetPassword />,
				permissions: []
			},
			{
				path: "/",
				permissions: [],
				element: (
					<Protected>
						<AppLayout />
					</Protected>
				),
				children: [
					{
						index: true,
						element: <Home />,
						permissions: []
					},
					{
						path: "user-management",
						element: <UserManagement />,
						permissions: ["super-admin", "sub-admin", "practice-administrator"]
					},
					{
						path: "audit-trail",
						element: <AuditTrail />,
						permissions: ["super-admin", "sub-admin"]
					},
					{
						path: "account-access",
						element: <AccountAccess />,
						permissions: ["super-admin", "sub-admin", "practice-administrator"]
					},
					{
						path: "create-user",
						permissions: ["super-admin", "sub-admin", "practice-administrator"],
						element: (
							<UserProvider>
								<AddUser editMode={false} />
							</UserProvider>
						)
					},
					{
						path: "create-user/:id",
						permissions: ["super-admin", "sub-admin", "practice-administrator"],
						element: (
							<UserProvider>
								<AddUser editMode={true} />
							</UserProvider>
						)
					},
					{
						path: "profile",
						permissions: [],
						children: [
							{
								path: "",
								element: <Profile />,
								permissions: []
							},
							{
								path: ":tab",
								element: <Profile />,
								permissions: []
							}
						]
					},
					{
						path: "account-access-2",
						permissions: ["super-admin"],
						element: <AccountAccess />
					},
					{
						path: "refresh-test-route",
						permissions: ["super-admin"],
						element: <TestRoute />
					}
					// {
					// 	path: "internal-user",
					// 	permissions: ["super-admin"],
					// 	element: (
					// 		<UserProvider>
					// 			<CreateInternalUser />
					// 		</UserProvider>
					// 	)
					// }
				]
			},
			{
				path: "*",
				permissions: [],
				element: <Navigate to="/sign-in" />
			}
		])
	}, [authState?.rolesInfo.roles])
	const routes = useRoutes(resolvedRoutes)
	return (
		<SContainer maxWidth={false} disableGutters>
			{routes}
			{/* <Routes>
				<Route path="sign-in" element={<Login />} />
				<Route path="forgot-password" element={<ForgotPassword />} />
				<Route path="reset-password" element={<ResetPassword />} />
				<Route
					path="/"
					element={
						<Protected>
							<AppLayout />
						</Protected>
					}
				>
					<Route index element={<Home />} />
					<Route path="user-management" element={<UserManagement />} />
					<Route path="audit-trail" element={<AuditTrail />} />
					<Route path="account-access" element={<AccountAccess />} />
					<Route
						path="create-user"
						element={
							<UserProvider>
								<AddUser editMode={false} />
							</UserProvider>
						}
					/>
					<Route
						path="create-user/:id"
						element={
							<UserProvider>
								<AddUser editMode={true} />
							</UserProvider>
						}
					/>
					<Route path="profile">
						<Route path="" element={<Profile />} />
						<Route path=":tab" element={<Profile />} />
					</Route>
					//TODO: need to decide how to utilize this
					<Route path="account-access-2" element={<AccountAccess />} />
					<Route path="refresh-test-route" element={<TestRoute />} />
					<Route
						path="internal-user"
						element={
							<UserProvider>
								<CreateInternalUser />
							</UserProvider>
						}
					/>
				</Route>
				<Route path="*" element={<Navigate to="/sign-in" />} />
			</Routes> */}
			<Snackbar
				anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
				open={appState?.alert.show}
				autoHideDuration={10000}
				key="bottom center"
				onClose={appState?.closeAlert}
				ClickAwayListenerProps={{ mouseEvent: false }}
			>
				<Alert severity={appState?.alert.status} variant="filled" elevation={6} onClose={appState?.closeAlert}>
					<AlertTitle>
						<Typography fontWeight={600} letterSpacing={2}>
							{`${appState?.alert.status?.charAt(0)?.toUpperCase()}${appState?.alert.status.substring(1)}`}
						</Typography>
					</AlertTitle>
					{appState?.alert.message}
				</Alert>
			</Snackbar>
		</SContainer>
	)
}

export default App
